import route from './route'

import DashboardBox from '../../components/dashboard-box'

import Auth from '../../mixins/auth'
import Notif from '../../mixins/notif'

export default {
  name: route.key,

  mixins: [
    Auth,
    Notif
  ],

  components: {
    DashboardBox
  },

  data() {
    return {
      data: null,
      isLoading: true,
      route
    }
  },

  computed: {
    dash() {
      let moneyOpt = { style: 'currency', currency: 'PHP' }
      let moneyFormat = new Intl.NumberFormat('en-US', moneyOpt)
      let numFormat = new Intl.NumberFormat()

      return this.data ? {
        players: this.data.total_player,
        sales: moneyFormat.format(this.data.daily_sales),
        commissions: moneyFormat.format(this.data.commissions),
        points: this.data.agent_points ?
          numFormat.format(this.data.agent_points)
            : 0
      } : null
    },

    role() {
      return this.$user.agent_type == 'agent' ?
        'Master agent' : 'Sub-agent'
    }
  },

  async mounted() {
    this.fetch()
  },

  methods: {
    async fetch() {
      try {
        this.data = await this.$store.dispatch('dashboard/get')
      } catch(e) {
        this.handleError(e)
      }

      this.isLoading = false
    }
  }
}



